import React from 'react'
import "bootstrap/dist/css/bootstrap.css";
import { Container, Row, Col, ListGroup, Badge } from "react-bootstrap";
import { Link } from "react-router-dom";
import Footer from './Footer'

function Home({surah, juz}) {
    return (
        <>
            <Container>
                <Row>
                    {surah.map((s, i) => (
                        <Col sm={6} lg={4} key={s.id_surah} className="mt-sm-3">
                            <Link to={`/surah/${s.id_surah}`}>
                                <ListGroup.Item className="m-2 text-dark rounded border-0 shadow-sm home-item">
                                    <Row>
                                        <Col xs={2} className="p-0 text-center">
                                            <Badge variant="success" className="rounded-circle">{s.id_surah}</Badge>
                                        </Col>
                                        <Col className="p-0 pr-3">
                                            <span>{s.nama_surah} ({s.jml_ayat})</span>
                                            <span className="float-right arab">{s.arab}</span>
                                            <span className="d-block">
                                                <small>{s.arti}</small>
                                                <small className="float-right">{s.tipe}</small>
                                            </span>
                                        </Col>
                                    </Row>
                                </ListGroup.Item>  
                            </Link>
                        </Col>
                    ))}
                </Row>
            </Container>
            <Footer />
        </>
    )
}

export default Home
