import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.css";
import { Container, Row, Col, ListGroup } from "react-bootstrap";
import axios from "axios";
import PaginateTop from './PaginateTop'
import PaginateBottom from './PaginateBottom'
import SidenavStrap from './SidenavStrap';
import Footer from './Footer'
import {API_URL} from '../Utils/utils'

export default function ListAyat({arti, latin, arab, isArtiOn, isLatinOn, isArabOn, surah, juz}) {
  
  const bismillah = "بِسْمِ اللّٰهِ الرَّحْمٰنِ الرَّحِيْمِ";
  let { id, tipe } = useParams();
  const [ayat, setAyat] = useState([]);
  const props = {arti, latin, arab, isArtiOn, isLatinOn, isArabOn, surah, juz, id, tipe};

  useEffect(() => {
    axios
      .get(`${API_URL}/api_alquran/${tipe}.php?id=${id}`)
      .then((res) => {
        const data = res.data;
        setAyat(data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [tipe, id]);

  return (
    <>
      <SidenavStrap {...props} />
      <PaginateTop {...props} />
      <Container>
        <Row className="justify-content-center">
          <Col sm={10}>
            <ListGroup>
              {ayat && ayat.map((ayat) => (
                <ListGroup.Item key={ayat.id}>
                  {ayat.no_ayat === "1"
                    ? 
                    <Col className="p-0">
                      {surah
                        .filter((s) => s.id_surah === ayat.id_surah)
                        .map((s) => (                         
                          <ListGroup.Item className="text-left mb-3 home-item border-0 shadow-sm rounded" key={s.id_surah}>
                            <Row>
                              <Col>
                                <span className="d-block">{s.nama_surah} ({s.jml_ayat})</span>
                                <small>{s.arti}</small>
                              
                              </Col>
                              <Col xs={2} className="text-center">
                                <p className="noarab-item">{s.no_arab}</p>
                              </Col>
                              <Col className="text-right">
                                <span className="d-block font-arab">{s.arab}</span>
                                <small>{s.tipe}</small>
                              </Col>
                            </Row>
                          </ListGroup.Item>
                        ))}
                      {!["1", "9"].includes(ayat.id_surah) && (
                          <ListGroup.Item className="border-top-0 border-left-0 border-right-0 pt-1 mb-3">
                            <p className="ayat-arab text-center mt-3">
                              {bismillah}
                            </p>
                          </ListGroup.Item>
                        )}
                    </Col>
                    : null}
                  <Col className="p-0">
                    {arab && (<p className="ayat-arab mb-1 text-right">
                      {ayat.ayat}
                      {" "}
                      <span className="badge-noarab rounded-circle">{ayat.no_arab}</span>
                    </p>)}
                    {latin && (<p className="ayat-latin mb-1">{ayat.latin}</p>)}
                    {arti && (<p className="ayat-arti mb-1">{ayat.arti}</p>)}
                  </Col>
                </ListGroup.Item>
              ))}
            </ListGroup>
          </Col>
        </Row>
      </Container>
      <PaginateBottom {...props} />
      <Footer />
    </>
  );
}
