import React from 'react'
import { Container, Row, Col, Form } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAngleLeft,
  faAngleRight,
} from "@fortawesome/free-solid-svg-icons";
import { Link, useParams, useHistory } from "react-router-dom";


export default function Pagination({surah, juz}) {

    let {id, tipe} = useParams();
    let history = useHistory();
    let next =  tipe==="surah" && id < 114 
                ? parseInt(id) + 1 
                : (tipe==="juz" && id < 30
                ? parseInt(id) + 1
                : id)
    let prev = id > 1 ? parseInt(id) - 1 : id

    const pushLink =(e)=> {
        history.push(`${e.target.value}`)
    }

    return (
        <>
        <Container>
            <Row className="justify-content-center list-ayat mb-4">
                <Col xs={11} sm={9}>
                    <Row className="paginate paginate-bg py-3 rounded shadow-sm">
                        <Col className="text-center mt-1">
                            <Link to={"/" + tipe + "/" + prev}>
                                <FontAwesomeIcon icon={faAngleLeft} />
                                {tipe==="surah" && surah
                                    .filter((s) => s.id_surah === String(prev))
                                    .map((s) => (
                                        <span key={s.id_surah}> {s.nama_surah} </span>
                                    ))}
                                {tipe==="juz" && juz
                                    .filter((j) => j.id_juz === String(prev))
                                    .map((j) => (
                                        <span key={j.id_juz}> {j.nama_juz} </span>
                                    ))}
                            </Link>
                        </Col>
                        <Col xs={5} className="text-center bg-white rounded">
                        <Form.Control
                            value={id}
                            as="select"
                            className="border-0"
                            size="sm"
                            custom onChange={pushLink}>
                            {tipe==="surah" && surah
                                .map((s) => (
                                    <option value={s.id_surah} key={s.id_surah}> {s.id_surah}. {s.nama_surah} </option>
                                    ))}
                            {tipe==="juz" && juz
                                .map((j) => (
                                    <option value={j.id_juz} key={j.id_juz}> {j.nama_juz} .{j.no_arab} </option>
                                ))}
                        </Form.Control>
                            {/* {tipe==="surah" && surah
                                .filter((s) => s.id_surah === id)
                                .map((s) => (
                                    <span key={s.id_surah}> {s.id_surah}. {s.nama_surah} </span>
                                ))}
                            {tipe==="juz" && juz
                                    .filter((j) => j.id_juz === id)
                                    .map((j) => (
                                        <span key={j.id_juz}> {j.nama_juz} .{j.no_arab} </span>
                                    ))} */}
                        </Col>
                        <Col className="text-center mt-1">
                            <Link to={"/" + tipe + "/" + next}>
                                {tipe==="surah" && surah
                                    .filter((s) => s.id_surah === String(next))
                                    .map((s) => (
                                        <span key={s.id_surah}> {s.nama_surah} </span>
                                    ))}
                                {tipe==="juz" && juz
                                    .filter((j) => j.id_juz === String(next))
                                    .map((j) => (
                                        <span key={j.id_juz}> {j.nama_juz} </span>
                                    ))}
                                <FontAwesomeIcon icon={faAngleRight} />
                            </Link>
                        </Col>
                    </Row>
                </Col>
            </Row> 
        </Container>
        </>
    )
}