import React, { Component } from "react";
import "../App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { Nav, Form, Col } from "react-bootstrap";
import $ from "jquery";
import { Link } from "react-router-dom";
import Navigation from "./Navigation";

class SidenavStrap extends Component {

  componentDidMount() {
    var prevScrollpos = window.pageYOffset;
    $(window).scroll(function (e) {
      var currentScrollPos = window.pageYOffset;
      if (prevScrollpos > currentScrollPos) {
        $("#navbar").css("top", "0");
      } else {
        $("#navbar").css("top", "-80px");
      }
      prevScrollpos = currentScrollPos;
    });
  }

  openNav =()=> {
    $("#mySidenav").css("left", "0");
    $("#mySidenav2").css("left", "-280px");
    $("#mySidenav3").css("right", "-280px");
  };
  closeNav =()=> {
    $("#mySidenav").css("left", "-280px");
  };
  openNav2 =()=> {
    $("#mySidenav2").css("left", "0");
    $("#mySidenav").css("left", "-280px");
    $("#mySidenav3").css("right", "-280px");
  };
  closeNav2 =()=> {
    $("#mySidenav2").css("left", "-280px");
  };
  openNav3 =()=> {
    $("#mySidenav3").css("right", "0");
    $("#mySidenav").css("left", "-280px");
    $("#mySidenav2").css("left", "-280px");
  };
  closeNav3 =()=> {
    $("#mySidenav3").css("right", "-280px");
  };

  render() {
    const {arti, latin, arab, isArtiOn, isLatinOn, isArabOn, id, tipe} = this.props;
    return (
      <div>
        <Navigation
          openNav={this.openNav}
          openNav2={this.openNav2}
          openNav3={this.openNav3}
        />

        <div id="mySidenav" className="sidenav shadow" style={{ left: "-280px" }}>
          <button href="#" className="btn closebtn float-right" onClick={this.closeNav}>
            &times;
          </button>
          <div className="clearfix"></div>
          <Nav defaultActiveKey="#" className="flex-column">
            {this.props.surah.map((s) => (
              <Link to={`/surah/${s.id_surah}`} key={s.id_surah} className={tipe==="surah" && s.id_surah===id ? "active" : ""}>
                {s.arab} <small>(</small>
                {s.no_arab} <small> ({s.nama_surah})</small>
              </Link>
            ))}
          </Nav>
        </div>

        <div id="mySidenav2" className="sidenav shadow" style={{ left: "-280px" }}>
          <button href="#" className="btn closebtn float-right" onClick={this.closeNav2}>
            &times;
          </button>
          <div className="clearfix"></div>
          <Nav defaultActiveKey="#" className="flex-column">
            {this.props.juz.map((j) => (
              <Link to={`/juz/${j.id_juz}`} key={j.id_juz} className={tipe==="juz" && j.id_juz===id ? "active" : ""}>
                {j.nama_juz} <small>(</small>
                {j.no_arab}
              </Link>
            ))}
          </Nav>
        </div>

        <div id="mySidenav3" className="sidenav shadow" style={{ right: "-280px" }}>
          <button href="#" className="btn closebtn" onClick={this.closeNav3}>
            &times;
          </button>
          <Col>
            <Form>
              <Form.Group controlId="Terjemahan">
                <Form.Check type="switch" id="custom-switch" label="Terjemahan" checked={arti} onChange={() => isArtiOn(!arti)} />
              </Form.Group>
              <Form.Group controlId="Latin">
                <Form.Check type="switch" id="custom-switch1" label="Latin" checked={latin} onChange={() => isLatinOn(!latin)} />
              </Form.Group>
              <Form.Group controlId="Arab">
                <Form.Check type="switch" id="custom-switch2" label="Arab" checked={arab} onChange={() => isArabOn(!arab)} />
              </Form.Group>
            </Form>
          </Col>
        </div>
      </div>
    );
  }
}
export default SidenavStrap;
