import React from 'react'
import "bootstrap/dist/css/bootstrap.css";
import { Jumbotron } from "react-bootstrap";


function Footer() {
    return (
        <>          
            <Jumbotron className="footer text-center mt-5 pt-4 pb-2 mb-0 rounded-0">
                <p>Copyright &copy; {new Date().getFullYear()}. Al-Qur'an Online - fsol.tech</p>
            </Jumbotron>
        </>
    )
}

export default Footer
