import React, {useState, useEffect} from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import ListAyat from "./Component/ListAyat";
import axios from "axios";
import Home from "./Component/Home";
import {API_URL} from './Utils/utils'

export default function App() {

  const [arti, isArtiOn] = useState(true);
  const [latin, isLatinOn] = useState(true);
  const [arab, isArabOn] = useState(true);
  const [surah, setSurah] = useState([]);
  const [juz, setJuz] = useState([]);
  const state = {arti, latin, arab, isArtiOn, isLatinOn, isArabOn, surah, juz};


  useEffect(() => {
    axios
      .get(`${API_URL}/api_alquran/surah.php`)
      .then((res) => {
        const surah = res.data;
        setSurah(surah );
      })
      .catch((error) => {
        console.log(error);
      });

    axios
      .get(`${API_URL}/api_alquran/juz.php`)
      .then((res) => {
        const juz = res.data;
        setJuz(juz);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path="/" children={<Home {...state} />} exact />
          <Route path="/:tipe/:id" children={<ListAyat {...state} />} />
        </Switch>
      </Router>
    </div>
  );
}