import React, {useEffect} from 'react'
import { Container, Row, Col } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAngleLeft,
  faAngleRight,
} from "@fortawesome/free-solid-svg-icons";
import { Link, useParams } from "react-router-dom";


export default function Pagination({surah, juz}) {

    let {id, tipe} = useParams();
    let next =  tipe==="surah" && id < 114 
                ? parseInt(id) + 1 
                : (tipe==="juz" && id < 30
                ? parseInt(id) + 1
                : id)
    let prev = id > 1 ? parseInt(id) - 1 : id

    useEffect(() => {
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth',
          });
    }, [id, tipe])

    return (
        <>
        <Container>
            <Row className="justify-content-around my-3">
                <Col xs={4} sm={2} className="text-center paginate-bg p-2 rounded">
                    <Link to={"/" + tipe + "/" + prev}>
                        <FontAwesomeIcon icon={faAngleLeft} />
                        {tipe==="surah" && surah
                            .filter((s) => s.id_surah === String(prev))
                            .map((s) => (
                                <span key={s.id_surah}> {s.nama_surah} </span>
                            ))}
                        {tipe==="juz" && juz
                            .filter((j) => j.id_juz === String(prev))
                            .map((j) => (
                                <span key={j.id_juz}> {j.nama_juz} .{j.no_arab} </span>
                            ))}
                    </Link>
                </Col>
                <Col xs={4} sm={2} className="text-center paginate-bg p-2 rounded">
                    <Link to={"/" + tipe + "/" + next}>
                        {tipe==="surah" && surah
                            .filter((s) => s.id_surah === String(next))
                            .map((s) => (
                                <span key={s.id_surah}> {s.nama_surah} </span>
                            ))}
                        {tipe==="juz" && juz
                            .filter((j) => j.id_juz === String(next))
                            .map((j) => (
                                <span key={j.id_juz}> {j.nama_juz} .{j.no_arab} </span>
                            ))}
                        <FontAwesomeIcon icon={faAngleRight} />
                    </Link>
                </Col>
            </Row> 
        </Container>
        </>
    )
}